.wrapper {
  width: 100%;
  height: 100vh;
  background-color: cadetblue;
  position: relative;
  overflow: hidden;
  .ball {
    width: 30px;
    height: 30px;
    background-color: rgba(255, 255, 255, 0.5);
    position: absolute;
    border-radius: 50%;
  }
  .ball.sticky {
    background-color: red;
  }
}
